import React, {useEffect} from 'react'
import './Message.css'

export default function Message({index, message, currentUser}) {
    useEffect(() => {
        console.log("currentUser: ", currentUser)
        
    }, [])
    return (
        <div className={`messageContainer ${(message.from===currentUser)?'right':'left'} ${(index===0)?'mt-auto':''}`}>
            <div className='senderName'>{message.from}</div>
            <div className='messageContent'>{message.message}</div>
        </div>
    )
}
