import React, { useState, useEffect, useRef } from 'react';
import socketio from 'socket.io-client';
import './Home.css'
import Message from '../Message/Message';

const Home = () => {

    const [isLoggedIn, setIsLoggedIn] = useState(false);
    const [name, setName] = useState('');
    const [socket, setSocket] = useState(null);
    const [messages, setMessages] = useState([]);
    const [inputMessage, setInputMessage] = useState('');

    const messagesContainerRef = useRef();

    const handleSubmit = (event)=>{
        event.preventDefault();
        console.log(name==='');
        if(name!==''){
            const socket = socketio('https://chat-app-ukd-v1.herokuapp.com/', {
                transports: ['websocket'],
                auth: {name}
                });
            console.log(socket)
            setSocket(prevSocket=>socket) 
        }       
    }

    useEffect(() => {
        if(socket){
            socket.on('connect', ()=>{
                console.log('socket connected', socket.id)
            });
            socket.on('name already taken', (data)=>{
                console.log(data);
                alert('username is already taken');
                // socket.off();
                socket.disconnect();
            });
            socket.on('connection-established', data=>{
                if(data===true){
                    setIsLoggedIn(true)
                }
                // socket.emit('message', {id: socket.id, message: 'Thanks for adding me!'})
            })
            socket.on('message', (data)=>{
                console.log(data);
                setMessages(prevState=>{
                    return [...prevState, data]
                });
                // console.log(messagesContainerRef.current.scrollHeight)
                messagesContainerRef.current.scrollTop = messagesContainerRef.current.scrollHeight;
            })
        }
        // return () => {
        //     // cleanup
        // }
    }, [socket])

    const handleSendMessage = (e) => {
        e.preventDefault();
        if(inputMessage!==''){
            socket.emit('message', {id: socket.id, message: inputMessage});
        }
        setInputMessage('');
    }

    return(
        (isLoggedIn===false)?
        <form onSubmit={(event)=>handleSubmit(event)} className='loginForm'>
            <input className='loginInput' placeholder='Enter the name' value={name} onChange={event=>setName(event.target.value)}></input>
            <button className='loginButton' type='submit'>Login</button>
        </form>
        :
        <div className='chat-window'>
            <div className='messages-container' ref={messagesContainerRef}>
                {messages.map((item, index)=><Message key={index} index={index} message={item} currentUser={name}/>)}
            </div>
            <form className='sendMessageContainer' onSubmit={e=>handleSendMessage(e)}>
                <input 
                    className='sendMessageInput'
                    placeholder='Start Typing...'
                    value={inputMessage}
                    onChange={e=>setInputMessage(e.target.value)}
                    />
                <button 
                    className='sendMessageButton'
                    type='submit'                    
                    >Send</button>
            </form>
        </div>
    )
}

export default Home;